import React, { useState } from 'react'
import PageHeaderAlt from 'components/layout-components/PageHeaderAlt'
import { Input, InputNumber, Table, Spin, Form, Modal, Radio, Button, Row, Col, Tooltip, Tag, Progress, Avatar, Menu, Card, message, Popconfirm } from 'antd';
import { AppstoreOutlined, UnorderedListOutlined, PlusOutlined } from '@ant-design/icons';
import { 
	StockOutlined, 
	CheckCircleOutlined, 
	ClockCircleOutlined,
	EditOutlined,
	DeleteOutlined
} from '@ant-design/icons';
import { COLORS } from 'constants/ChartConstant';
import Flex from 'components/shared-components/Flex';
import EllipsisDropdown from 'components/shared-components/EllipsisDropdown'
import { DataService } from "services/sites.service";
import NumberFormat from 'react-number-format';

const VIEW_LIST = 'LIST';
const VIEW_GRID = 'GRID';

const ItemAction = ({id, removeId, editId}) => (
	<EllipsisDropdown 
		menu={
			<Menu>
				<Menu.Item key="1" onClick={() => editId(id.data())}>
					<EditOutlined />
					<span>Edit</span>
				</Menu.Item>
				<Menu.Divider />
				<Menu.Item key="2">
					<Popconfirm title="Sure to delete?" 
							onConfirm={() => removeId(id.id)
						}>
						<DeleteOutlined />
						<span>Delete Site</span>
					</Popconfirm>
				</Menu.Item>
			</Menu>
		}
	/>
)

const ItemHeader = ({name, category}) => (
	<div>
		<h4 className="mb-0">{name}</h4>
		<span className="text-muted">{category}</span>
	</div>
)

const Empty = () =>(
	<Table 
		columns={[]} 
		dataSource={[]} 
		showHeader={false}
	/>
)

const ItemInfo = ({attachmentCount, completedTask, totalTask, dayleft}) => (
	<Flex alignItems="center">
		<div className="mr-3">
			<Tooltip title="Estimated Amount">
				<StockOutlined className="text-muted font-size-md"/>
				<span className="ml-1 text-muted"><NumberFormat displayType={'text'} value={attachmentCount} thousandSeparator={true}/></span>
			</Tooltip>
		</div>
		<div className="mr-3">
			<Tooltip title="Paid Clients">
				<CheckCircleOutlined className="text-muted font-size-md"/>
				<span className="ml-1 text-muted">{completedTask}/{totalTask}</span>
			</Tooltip>
		</div>
		<div>
		<Tag className='bg-gray-lightest' color=''>
			<ClockCircleOutlined />
			<span className="ml-2 font-weight-semibold"><NumberFormat displayType={'text'} value={dayleft} thousandSeparator={true}/> left</span>
		</Tag>
		</div>
	</Flex>
)

const ItemProgress = ({progression}) => (
	<Progress percent={progression} strokeColor={getProgressStatusColor(progression)} size="small"/>
)

const ItemMember = ({member,hideName}) => !hideName ? (<span className="ml-2">m<sup>3</sup> = <NumberFormat displayType={'text'} value={member.price} suffix=" Rwf" thousandSeparator={true}/></span>) : null


const ListItem = ({ site, removeId, editId,countInSite, estimatedInSite, siteMonthlyPaid, paidClients }) => (
	<div className="bg-white rounded p-3 mb-3 border">
		<Row align="middle">
    	<Col xs={24} sm={24} md={5}>
				<ItemHeader name={site.data().name} category={site.data().siteId} />
			</Col>
			<Col xs={24} sm={24} md={9}>
				<ItemInfo 
					attachmentCount={estimatedInSite}
					completedTask={paidClients}
					totalTask={countInSite(site.id)}
					dayleft={estimatedInSite-siteMonthlyPaid}
				/>
			</Col>
			<Col xs={24} sm={24} md={5}>
				<ItemProgress progression={(siteMonthlyPaid/estimatedInSite)*100} />
			</Col>
			<Col xs={24} sm={24} md={3}>
				<div className="ml-0 ml-md-3">
					<ItemMember member={site.data()}/>
				</div>
			</Col>
			<Col xs={24} sm={24} md={2}>
				<div className="text-right">
					<ItemAction id={site} removeId={removeId} editId={editId}/>
				</div>
			</Col>
		</Row>
	</div>
)

const GridItem = ({ site, removeId, editId,countInSite, estimatedInSite, siteMonthlyPaid, paidClients }) => (
	<Card>
		<Flex alignItems="center" justifyContent="between">
			<ItemHeader name={site.data().name} category={site.data().siteId} />
			<ItemAction id={site} removeId={removeId} editId={editId}/>
		</Flex>
		<div className="mt-2">
			<ItemInfo 
				attachmentCount={estimatedInSite}
				completedTask={paidClients}
				totalTask={countInSite(site.id)}
				dayleft={estimatedInSite-siteMonthlyPaid}
			/>
		</div>
		<div className="mt-3">
			<ItemProgress progression={(siteMonthlyPaid/estimatedInSite)*100} />
		</div>
		<div className="mt-2">
			<ItemMember member={site.data()}/>
		</div>
	</Card>
)

const getProgressStatusColor = progress => {
	if(progress >= 80) {
		return COLORS[1]
	}
	if(progress < 60 && progress > 30) {
		return COLORS[3]
	}
	if(progress < 30) {
		return COLORS[2]
	}
	return COLORS[0]
}

const ProjectList = ({systemData}) => {

	const [view, setView] = useState(VIEW_GRID);
	const [month, setMonth] = useState(new Date(new Date().getFullYear(), new Date().getMonth
	()).setDate(1))
	const [form] = Form.useForm();
	const [visible, setVisible] = useState(false);
	const [confirmLoading, setConfirmLoading] = useState(false);
	const [editingKey, setEditingKey] = useState('');
	const [myId, setMyId] = useState('');
	const [siteId, setSiteId] = useState('');
	const { sites, sitesLoading, countInSite, estimatedInSite, siteMonthlyPaid, siteMonthlyPaidClients  } = systemData;
	const edit = record => {
		form.setFieldsValue({ Oldname:record.name,Oldprice:record.price });
		setEditingKey(record._id);
		setSiteId(record.siteId)
		setMyId(record.myId)
	  };
	
	  const cancel = () => {
		setEditingKey('');
		form.setFieldsValue({Oldname:null,Oldprice:null});
	  };
	
	  const save = async () => {
		try {
		  const row = await form.validateFields();
		  let name = row.Oldname.slice(0,1) === ' ' ? row.Oldname.slice(1) : row.Oldname;
		  let price = row.Oldprice;
		  if (name==='' || !price) return;
		  let siteData = {name, price, siteId, myId}
		  DataService.update(editingKey,siteData).then(result => {
			if(result){
				setEditingKey('');
				form.setFieldsValue({Oldname:null,Oldprice:null});
				message.success(`Site updated`);
			}
			else
				message.error(`Unable to update, please check your internet connection`);
		  })
		} catch (errInfo) {}
	  };
	
	  const showModal = () => {
		setVisible(true);
	  }
	  const handleOk = async e => {
		try {
		  const values = await form.validateFields();
		  let name = values.name.slice(0,1) === ' ' ? values.name.slice(1) : values.name;
		  let price = values.price;
		  if (name==='' || !price) return;
		  setConfirmLoading(true);
		  let previous = sites.docs.length > 0 ? sites.docs[sites.docs.length-1].data().myId : 0;
		  let siteId = (previous<9?'0':'')+parseInt(previous+1);
		  let myId = parseInt(previous+1);
		  let siteData = {name, price, siteId, myId}
		  DataService.create(siteData).then(result => {
			setConfirmLoading(false);
			if(result){
				setVisible(false);
				form.setFieldsValue({name:null,price:null});
				message.success(`${name} created`);
			}
			else
				message.error(`Unable to create, please check your internet connection`);
		  })
		} catch (errInfo) {}
	  }
	  const handleCancel = () => {
		setVisible(false);
		form.setFieldsValue({name:null,price:null});
	  }

	const onChangeProjectView = e => {
		setView(e.target.value)
	}

	const deleteItem = id =>{
		DataService.remove(id)
		message.success(`Site deleted`);
	}

	return (
		<>
			<PageHeaderAlt className="border-bottom">
				<div className="container-fluid">
					<Flex justifyContent="between" alignItems="center" className="py-4">
						<h2>Sites</h2>
						<div>
							<Radio.Group defaultValue={VIEW_GRID} onChange={e => onChangeProjectView(e)}>
								<Radio.Button value={VIEW_GRID}><AppstoreOutlined /></Radio.Button>
								<Radio.Button value={VIEW_LIST}><UnorderedListOutlined /></Radio.Button>
							</Radio.Group>
							<Button type="primary" className="ml-2" onClick={showModal}>
								<PlusOutlined />
								<span>New</span>
							</Button>
						</div>
					</Flex>
				</div>
			</PageHeaderAlt>
			<div className={`my-4 ${view === VIEW_LIST? 'container' : 'container-fluid'}`}>
				{
					!sitesLoading ? ( sites.docs.length > 0 ? (view === VIEW_LIST ?
					sites.docs.map((elm, index) => {
						let estimated = estimatedInSite(elm.id,month);
						let paidMonth = siteMonthlyPaid(elm.id,month);
						let paidClients = siteMonthlyPaidClients(elm.id,month);
						return(<ListItem site={elm} removeId={deleteItem} editId={edit} key={elm.id} countInSite={countInSite} estimatedInSite={estimated} siteMonthlyPaid={paidMonth} paidClients={paidClients}/>)})
					:
					<Row gutter={16}>
						{sites.docs.map((elm, index) => {
							let estimated = estimatedInSite(elm.id,month);
							let paidMonth = siteMonthlyPaid(elm.id,month);
							let paidClients = siteMonthlyPaidClients(elm.id,month);
							return (
							<Col xs={24} sm={24} lg={8} xl={8} xxl={6} key={elm.id}>
								<GridItem site={elm} removeId={deleteItem} editId={edit} countInSite={countInSite} estimatedInSite={estimated} siteMonthlyPaid={paidMonth} paidClients={paidClients}/>
							</Col>
						)})}
					</Row>)
					 : <Empty />)
					 : <Spin />
				}
			</div>
			<Form form={form} component={false} layout="vertical">
              <Modal
                title="New Site"
                visible={visible}
                onOk={handleOk}
                confirmLoading={confirmLoading}
                onCancel={handleCancel}
              >
                <Row gutter={16}>
                  <Col xs={24} sm={24} md={24}>
                    <Form.Item name="name" label="Site Name" rules={[
                      {
                        required: true,
                        message: `Site Name is required.`,
                      },
                    ]}>
                      <Input
                        className="w-100"
                      />
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={24} md={24}>
                    <Form.Item name="price" label={(<span>Monthly m<sup>3</sup> price</span>)} rules={[
                      {
                        required: true,
                        message: (<span>Monthly m<sup>3</sup> price is required</span>),
                      },
                    ]}>
                      <InputNumber
								className="w-100"
								min={0}
								parser={value => value.replace(/\$\s?|(,*)/g, '')}
								formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							/>
                    </Form.Item>
                  </Col>
                </Row>
              </Modal> 
              <Modal
                title="Update Site"
                visible={editingKey}
                onOk={save}
                onCancel={cancel}
                footer={[
                  <Button key="back" onClick={cancel}>
                  Cancel
                </Button>,
                  <Button key="submit" type="primary" loading={confirmLoading} onClick={save}>
                    OK
                  </Button>,
                ]}
              >
                <Row gutter={16}>
                  <Col xs={24} sm={24} md={24}>
                    <Form.Item name="Oldname" label="Site name" rules={[
                      {
                        required: true,
                        message: `Site name is required.`,
                      },
                    ]}>
                       <Input className="w-100"/>
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={24} md={24}>
                    <Form.Item name="Oldprice" label={(<span>Monthly m<sup>3</sup> price</span>)} rules={[
                      {
                        required: true,
                        message: (<span>Monthly m<sup>3</sup> price is required</span>),
                      },
                    ]}>
                      <InputNumber
								className="w-100"
								min={0}
								parser={value => value.replace(/\$\s?|(,*)/g, '')}
								formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							/>
                    </Form.Item>
                  </Col>
                </Row>
              </Modal> 
            </Form> 
		</>
	)
}

export default ProjectList
